целях реализации жилищных госпрограмм, получения доходов акционерами и обеспечения снижения рисков.\nСоциальный фонд недвижимости вправе сдавать в аренду недвижимое имущество, приобретённое или построенное за счёт средств от акций.\n\nПроект документа обсуждается. Возможно, таких фондов будет несколько.")])])]),_c('div',{staticClass:"main__new"},[_c('img',{staticClass:"main__mb",attrs:{"src":require("../../assets/images/kaspigroup/news__4.jpeg"),"alt":""}}),_c('div',{staticClass:"main__description"},[_c('p',[_vm._v("На выбор квартиры влияет множество факторов, в том числе и этаж, на котором вы хотели бы жить.\n⠀\nПри выборе квартиры какие этажи для вас предпочтительнее?\n⠀\n1️⃣ Выберу нижние этажи. Легче подняться пешком\n⠀\n2️⃣ Золотая середина. Оптимальный для меня выбор\n⠀\n3️⃣ Чем выше, тем лучше. Отличный вид из окна")])])]),_c('div',{staticClass:"main__new"},[_c('img',{staticClass:"main__mb",attrs:{"src":require("../../assets/images/kaspigroup/kaspi_news2.jpg"),"alt":""}}),_c('div',{staticClass:"main__description"},[_c('p',[_vm._v("Почему лучше купить жилье в новом доме🤔\n⠀\n- Покупая квартиру в новостройке, как правило, у вас есть большой выбор возможных планировок квартир и можно выбрать то, что подходит именно вам. В случае со \"вторичкой\" вы получаете однотипные планировки — маленькая кухня, неудобные прихожие и т.д.🔑\n⠀\n- В новом доме стены, лифты, лестничные пролеты будут чистыми. Состояние входных групп сильно влияет на общее впечатление от места проживания.🚪\n⠀\n- В новостройках люди будут примерно одного уровня жизни. Соответственно, меньше вероятности, что соседи у вас будут алкоголиками или наркоманами.💳\n⠀\n- Жилье в новых домах не имеет юридической истории и обременений.📃")])])]),_c('div',{staticClass:"main__new"},[_c('img',{staticClass:"main__mb",attrs:{"src":require("../../assets/images/kaspigroup/kaspi_news3.jpg"),"alt":""}}),_c('div',{staticClass:"main__description"},[_c('p',[_vm._v("У кого нельзя покупать квартиру? Давайте сегодня поговорим про покупку квартиры на вторичном рынке недвижимости. К сожалению, на практике нередко встречаются случаи, когда люди, наконец-таки отыскав понравившуюся квартиру, не обращали внимание на продавца, как на личность (поведение, статус, возраст и т.п.), так как документы были \"чистыми\", и лишались не только денег, но и жилплощади.\n⠀\nПри таких больших денежных вложениях, как покупка недвижимости, надо обращать внимание на все детали и нюансы, в том числе и на самого продавца. Сегодня мы рассмотрим типы людей, которых лучше сторониться при покупке квартиры. Или, по крайней мере, со всей тщательностью подойти к проверке всех нюансов.\n⠀\n📍 Доверенность\n⠀\nКонечно, бывают случаи, когда владелец квартиры уже давно находится на ПМЖ в другой стране и поэтому поручает продажу квартиры доверенному лицу. Но также есть статистика, когда доверенность оформляют от лица недееспособных людей или заверяют ее нотариусы, не имеющие лицензии. Или, к примеру, сам владелец квартиры оказывается мошенником и отзывает доверенность непосредственно перед сделкой.\n⠀\nИтог в таких ситуациях практически всегда одинаковый: суд встает на сторону владельца квартиры, а покупателю предстоят долгие разбирательства, чтобы вернуть обратно свои деньги, и не всегда это получается.\n⠀\n📍 Преклонный возраст\n⠀\nНе стоит покупать квартиру у человека, который старше 70 лет. Что может случиться? Ну, например, близкий родственник может приложить усилия и признать пожилого человека недееспособным, соответственно сделка аннулируется. Встречаются и такие ситуации, когда спустя год после продажи квартиры и спустя полгода после кончины человека, его дочери/сыновья умудрялись оспорить \"трезвый ум и здравую память\" родителей и признать продажу недействительной.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"main__links"},[_c('a',{attrs:{"href":"https://wa.me/+77076500104","target":"_blank"}},[_c('i',{staticClass:"fab fa-telegram"})]),_c('a',{